import React from 'react';
import Welcome from './Welcome';
import Notes from './Notes';
import Question from './Question';

class ControlApp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      appActive: 0
    };
    this.nextNotes = this.nextNotes.bind(this);
    this.restart = this.restart.bind(this);
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState(state => ({appActive: state.appActive + 1}));
    }, 2200);
  }

  nextNotes() {
    this.setState(state => ({appActive: state.appActive + 1}));
  }

  restart() {
    this.setState({appActive: 1});
  }
  
  render() {
    return (
      <>
        <RenderApp restart={this.restart} onNextNotes={this.nextNotes} appActive={this.state.appActive} />
      </>
    )
  }
}

function RenderApp(props) {
  return (
    <>
      {
        props.appActive === 2 ?
        <Question restart={props.restart} /> : 
        props.appActive ?
        <Notes appActive={props.appActive} onNextNotes={props.onNextNotes} /> :
        <Welcome />
      }
    </>
  );
}

export default ControlApp;