import React from 'react';
import myData from '../data/data.json';
import xanana from '../img/xanana.jpg';
import lisan from '../img/uma_lisan.jpeg';
import nainf from '../img/nain_feto_rm.jpg';
import pelo from '../img/ai_pelo.jpg';
import horta from '../img/ramos_horta.jpg'
import dili from '../img/capital_dili.jpg'
import papa from '../img/papa_ii.jpg';
import tebedai from '../img/tebe_dai.jpg';
import correct from '../img/correct.png';
import incorrect from '../img/cancel.png';
import cristo from '../img/cristo.jpg';
import invasion from '../img/invasion_1975.jpg';
import Score from './Score';

class Question extends React.Component {

  constructor(props) {
    super(props);
    this.state = {nextQuestion: 0, captureQuestion: '', yourScore: 0, isCorrect: true};
    this.nextQ = this.nextQ.bind(this);
    this.handledChange = this.handledChange.bind(this);
    this.handledSubmit = this.handledSubmit.bind(this);
  }
  
  nextQ() {
    this.setState(state => ({nextQuestion: state.nextQuestion + 1}));
  }

  handledChange(e) {
    this.setState({captureQuestion: e.target.value});
  }

  handledSubmit(e) {
    e.preventDefault();
    this.nextQ();
    const check = myData[this.state.nextQuestion].answer[`${this.state.captureQuestion}`];
    this.setState({isCorrect: check === 'loos' ? true : false});

    if (check === "loos") {
      this.setState(state => ({yourScore: state.yourScore + 10}));
    } else {
      this.setState(state => ({yourScore: state.yourScore}));
    }
    
    this.setState({captureQuestion: ''});
  }

  render() {
    const restart = this.props.restart;
    return (
      <>
      {
      myData.length === this.state.nextQuestion ?
      <Score restart={restart} yourScore={this.state.yourScore} /> :
      <QuestionHere isCorrect={this.state.isCorrect} score={this.state.yourScore} i={this.state.nextQuestion} handledChange={this.handledChange} handledSubmit={this.handledSubmit} />
    }
    </>
    );
  }
}

function ShowImage(props) {
  const imgActive = props.imgActive;

  const images = (
    imgActive === 0 ? xanana
    : imgActive === 1 ? lisan
    : imgActive === 2 ? nainf
    : imgActive === 3 ? pelo 
    : imgActive === 4 ? horta
    : imgActive === 5 ? dili
    : imgActive === 6 ? papa 
    : imgActive === 7 ? tebedai
    : imgActive === 8 ? cristo
    : imgActive === 9 ? invasion : null
  );

  return (
    <img src={images} alt={images} />
  );
}


function QuestionHere(props) {
  const i = props.i;

  const badScore = {
    fontSize: "1.3rem",
    color: "red"
  };

  const goodScore = {
    fontSize: "1.3rem",
    color: "green"
  }

  return (
    <div className="question">
      <ShowImage imgActive={i} />
      <form onSubmit={props.handledSubmit}>
        <label>{myData[i].question}</label> <br />
        {
          Object.keys(myData[i].answer).map((value, index) => <LoadQuestion key={index.toString()} value={value} handledChange={props.handledChange} />)
        }
        <input type="submit" value="next" />
      </form>

      <div className="score-info">
        {
        props.isCorrect ?
        <>
          <p style={goodScore}>Hau nia valor {props.score}</p>
          {
            props.score > 0 &&
            <img src={correct} alt="correct" />
          }
        </> :
        <>
          <p style={badScore}>Hau nia valor {props.score}</p>
          <img src={incorrect} alt="incorrect" />
        </>
      }
      </div>
    </div>
  );
}

function LoadQuestion(props) {
  const value = props.value;
  return (
    <> 
      <label>
        <input type="radio" name="zona tl" value={value} onChange={props.handledChange} /> 
        {value}
        </label> <br /> 
    </>
  );
}

export default Question;