export default function Score(props) {
  return (
    <div className="score">
      {
        props.yourScore >= 60 ?
        <>
          <h1>Parabens!</h1>
          <p>Ita nia valor {props.yourScore}</p>
        </> :
        <>
          <h1>Deskulpa!</h1>
          <p>Ita nia valor {props.yourScore}<br />ita laduun iha koniesimentu neebe diak ba iha ita nia rain!</p>
        </>
      }
      <p>{props.test}</p>
      <button type="button" onClick={props.restart}>Fila....</button>
    </div>
  );
}